@use '../base/variable' as v;
@use '../base/mixins' as m;

.filtros{
    margin-top: 2rem;
    background-color: lighten(v.$blanco, 20%);
    border-radius: 1rem;
    padding: 2rem;

    .filtros-inputs{
        @include m.tablet {
            display:flex;
            align-items: center;
            gap: 3rem;
        }
    }

    h2{
        color: v.$azul;
        font-size: 2.8rem;

        @include m.tablet {
            margin:0;
        }
    }

    .campo{
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;

        @include m.tablet {
            margin-bottom: 0;
        }
    }

    input[type="radio"]{
        accent-color: v.$azul;
        width: 1.6rem;
        cursor: pointer;
        height: 1.6rem;
    }
}